import React, {Fragment} from 'react';
import { Button, Form, FormControl, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { Link } from 'react-router-dom';
function Mynavbar(){
    return(
        <Fragment>
            <Navbar bg="light" expand="lg">
            <Link to="/"><Navbar.Brand>Galaxy games</Navbar.Brand></Link>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                <Link className="nav-link" to="/edit">Editar juegos</Link>
                <Link className="nav-link" to="/about">Acerca de</Link>
                <Link className="nav-link" to="/entrar">Inicio de sesion</Link>
                <NavDropdown title="Generos" id="basic-nav-dropdown">
                    <NavDropdown.Item href="#action/3.1">Aventura</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2">Accion</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.3">Terror</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action/3.4">Estrategia</NavDropdown.Item>
                </NavDropdown>
                </Nav>
                <Form inline>
                <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                <Button variant="outline-success">Busqueda</Button>
                </Form>
            </Navbar.Collapse>
            </Navbar>
        </Fragment>);

}
export default Mynavbar;