import React,{useState} from 'react';
import { Route, Switch } from 'react-router';
import About from './About';
import Content from './Content';
import Edition from './Edition';
import Detail from './Detail';
import Entrar from './Entrar';
import Editarjuego from './Editarjuego';
import { Fragment } from 'react';
const MainContainer = () => {
    const [videojuegos, guardarjuegos] = useState([
        {id:1,juego:'Rocket league',edad:'8 años', genero:'Estrategia', detalle:'Rocket League es un videojuego que combina el fútbol con los vehículos. Fue desarrollado por Psyonix y lanzado el 7 de julio del 2015. Se encuentra disponible en español, tiene modos de juego cooperativo, de un jugador y en línea.',imagen:'https://i.blogs.es/08015f/rocket-league/1366_2000.jpeg'}, 
        {id:2,juego:'Fortnite',edad:'15 años',genero:'Accion',detalle:'Fortnite es un videojuego del año 2017 desarrollado por la empresa Epic Games, lanzado como diferentes paquetes de software que presentan diferentes modos de juego, pero que comparten el mismo motor de juego y mecánicas. Fue anunciado en los Spike Video Game Awards en 2011. ', imagen:'https://cdn2.unrealengine.com/br16-socialshare-lineup1-1920x1080-cc758b8820b8.jpg'},
        {id:3,juego:'Dead by daylight',edad:'+18 años',genero:'Terror',detalle:'Dead by Daylight es un videojuego de horror de supervivencia desarrollado por Behaviour Interactive y publicado por Starbreeze Studios.​',imagen:'https://mobile.deadbydaylight.com/wp-content/uploads/2020/04/web-image.jpg'},
        {id:4,juego:'Duck game',edad:'+5 años',genero:'Accion',detalle:'Duck Game es un videojuego de acción desarrollado por Landon Podbielski y distribuido por Adult Swim Games. El juego fue lanzado para Ouya en 2014, y para Microsoft Windows en 2015.', imagen:'https://cdn02.nintendo-europe.com/media/images/10_share_images/games_15/nintendo_switch_download_software_1/H2x1_NSwitchDS_DuckGame_image1600w.jpg'},
        {id:5,juego:'Dragon Ball Z Kakarot',edad:'+15 años',genero:'Aventura',detalle:'Dragon Ball Z: Kakarot es un videojuego de rol de acción, desarrollado por CyberConnect2 y publicado por Bandai Namco Entertainment.​ Basado en la franquicia de Dragon Ball, se lanzó para Microsoft Windows, PlayStation 4 y Xbox One.​​​ ', imagen:'https://i.blogs.es/9a1106/dbzkakarotjpcover-tiny/450_1000.jpeg'},
        {id:6,juego:'Among Us',edad:'+8 años',genero:'Accion',detalle:'Among Us​ es un videojuego de género party y multijugador en línea desarrollado por la compañía estadounidense InnerSloth y distribuido entre junio y noviembre de 2018 para las plataformas Android, iOS y PC.', imagen:'https://cdn02.nintendo-europe.com/media/images/10_share_images/games_15/nintendo_switch_download_software_1/H2x1_NSwitchDS_AmongUs.jpg'},
        {id:7,juego:'Minecraft',edad:'+5 años',genero:'Aventura',detalle:'Minecraft es un videojuego de construcción, de tipo «mundo abierto» o sandbox creado originalmente por el sueco Markus Persson, ​ y posteriormente desarrollado por su empresa, Mojang Studios.', imagen:'https://d2e111jq13me73.cloudfront.net/sites/default/files/styles/amp_blog_image_large/public/blog/csm-blog/parentsultimateguide-minecraft-blog.jpg?itok=2beDaNo7'},
        {id:8,juego:'Outlast',edad:'+18 años',genero:'Terror',detalle:'Outlast 2 es un videojuego de horror de supervivencia en primera persona desarrollado y publicado por Red Barrels Games.', imagen:'data:image/jpeg;base64,'},
        {id:9,juego:'Outlast 2',edad:'+18 años',genero:'Terror',detalle:'Outlast 2 es un videojuego de horror de supervivencia en primera persona desarrollado y publicado por Red Barrels Games.', imagen:'https://media.redadn.es/imagenes/outlast-ii-mac-nintendo-switch-pc-ps4-xbox-one_315615.png'},
        {id:10,juego:'Fall guys',edad:'+8 años',genero:'Aventura',detalle:'Caminar', imagen:'https://img.youtube.com/vi/tmWwGP5mROg/maxresdefault.jpg'},
        {id:11,juego:'The witcher 3',edad:'+18 años',genero:'Aventura',detalle:'Caminar', imagen:'https://www.comunidadxbox.com/wp-content/uploads/2020/05/The-Witcher-3-Wild-Hunt-Comunidad-Xbox.jpg'},
        {id:12,juego:'Shadow of the Tomb Raider',edad:'18 años',genero:'Aventura',detalle:'Caminar', imagen:'https://cdn.akamai.steamstatic.com/steam/apps/750920/capsule_616x353.jpg?t=1617889764'},
    ]);
    const deleteGame= id=>{
        const listaJuegos= videojuegos.filter(videojuego=>videojuego.id!==id);
        guardarjuegos(listaJuegos);
    
    }

    const updateGame = game => {
        const listaJuegos= videojuegos.filter(videojuego=>videojuego.id!==game.id);
        listaJuegos.push(game);
        guardarjuegos(listaJuegos);
    }
    return ( 
        <Fragment>
            <Switch>
                <Route exact path="/" component={() => <Content videojuegos={videojuegos} />}/>
                <Route path="/about" component={About}/>
                <Route path="/edit" component={() => <Edition videojuegos={videojuegos} deleteGame={deleteGame} />}/>
                <Route path="/detail/:id" render={(props) => <Detail {...props} videojuegos={videojuegos} />} />
                <Route path="/Entrar" component={Entrar}/>
                <Route path="/editarjuego/:id" render={(props) => <Editarjuego {...props} updateGame={updateGame} videojuegos={videojuegos} />} />
            </Switch>
        </Fragment>
     );
}
 
export default MainContainer;