import React, { useState, Fragment }from 'react';
import { Button, Card } from 'react-bootstrap';
import Carru from './Carru';
import { Link } from 'react-router-dom';
import './content.css';
<link rel="icon" href="%SRC_URL%/favicon.ico" />
function Content({videojuegos}){

    

    return(
        <Fragment>
            <Carru/>
        <div className='row'>        
            {videojuegos.map (videojuego => (
                <div className='col-xs-12 col-md-6 col-lg-4'>
                    <Card style={{ width: '16rem' }}>
                    <Card.Img variant="top" src={videojuego.imagen} />
                    <Card.Body>
                        <Card.Title>{videojuego.juego} {videojuego.edad}</Card.Title>
                        <Card.Text>
                            {videojuego.detalle}.
                        </Card.Text>
                        <Link to={`/detail/${videojuego.id}`}>
                        <Button variant="primary">Comprar</Button>
                     </Link>
                    </Card.Body>
                    </Card>
                </div>
            ))}
        </div>
        </Fragment>
        
    );
}

export default Content;