import React from 'react';
import {Button, Table,} from 'react-bootstrap';
import { Link } from 'react-router-dom';
const Edition = ({videojuegos,deleteGame}) => {
    return ( 
    <Table responsive>
        <thead>
            <tr>
            <th>Id</th>
            <th>Nombre</th>
            <th>Edad</th>
            <th>Detalle</th>
            <th></th>
            <th></th>
            </tr>
        </thead>
        <tbody>
            {
                videojuegos.map(videojuego=>
                    <tr>
                    <td>{videojuego.id}</td>
                    <td>{videojuego.juego}</td>
                    <td>{videojuego.edad}</td>
                    <td>{videojuego.detalle}</td>
                    <td><Button
                    onClick={()=>deleteGame(videojuego.id)}
                    >Eliminar</Button></td>
                     <Link to={`/editarjuego/${videojuego.id}`}>
                        <Button variant="primary">Editar</Button>
                     </Link>
                    </tr>
                    )
            }
            
        </tbody>
    </Table>
     );
}
 
export default Edition;