import React from 'react';
import { Fragment } from 'react';
import {Accordion,Card,Button,Badge,Image} from 'react-bootstrap';
import './content.css';
const Detail = (props) => {
    const videojuego = props.videojuegos.find( videojuego => videojuego.id == props.match.params.id);
    if (!videojuego){
        return(
            <h1>No se encontro el videojuego</h1>
        )
    }
    return ( 
      <div Nameclass="bg-primary" >
        <Fragment>
        <h1>Nombre {videojuego.juego}</h1>
        <h1>Id. {videojuego.id}</h1>
        <h1>Edad requerida {videojuego.edad}</h1>
        <Image src="holder.js/100px250" src={videojuego.imagen} alt={videojuego.juego}/>
        <h1>Precio: $100 {videojuego.detalles}</h1>
        <h1>Es un juego de {videojuego.genero}</h1>
        <Badge pill variant="primary">
    Accion
  </Badge>{' '}
        <Accordion defaultActiveKey="0">
  <Card>
    <Card.Header>
      <Accordion.Toggle as={Button} variant="link" eventKey="0">
        Detalles del videjuego
      </Accordion.Toggle>
    </Card.Header>
    <Accordion.Collapse eventKey="0">
      <Card.Body>{videojuego.detalle}</Card.Body>
    </Accordion.Collapse>
  </Card>
</Accordion>
        </Fragment>
      </div>
     );
}
 
export default Detail;