import {Form,Button, Alert} from 'react-bootstrap';
import './entrar.css';
import React,{useState} from 'react';



const Editarjuego = (props) => {

  const [error, seterror] = useState(false)
  const [success, setsuccess] = useState(false)
    
    let videojuego = props.videojuegos.find( videojuego => videojuego.id == props.match.params.id);
    if (!videojuego){
        return(
            <h1>No se encontro el videojuego</h1>
        )
    }
    const {juego,edad,detalle,id}=videojuego

    const actualizarestado=e =>{
        videojuego[e.target.name]= e.target.value
    }

    const actualizarJuego= () => {
      if(videojuego.juego.trim() === '' ||
          videojuego.detalle.trim() === '' ||
          videojuego.edad.trim() === ''){
        seterror(true);
        setsuccess(false);
        return;
      }
      seterror(false);
      setsuccess(true);
      props.updateGame(videojuego);
    }
    return ( 
      <div>
         {
            error?<Alert variant="danger">
            Todos los campos son obligatorios!
          </Alert>:null
          }
          {
            success?<Alert variant="success">
            Juego actualizado con exito!
          </Alert>:null
          }
          <div className="row justify-content-md-center">
          <Form className="form-login">
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Nombre</Form.Label>
            <Form.Control type="text" placeholder="Enter name" name="juego" onChange={actualizarestado} defaultValue={juego} />
            <Form.Text className="text-muted" >
              No compartas tu correo electronico con nadie
            </Form.Text>
          </Form.Group>
        
          <Form.Group controlId="formBasicPassword">
            <Form.Label>Edad</Form.Label>
            <Form.Control type="text" placeholder="Age" name="edad" onChange={actualizarestado} defaultValue={edad}/>
          </Form.Group>
          <Form.Group controlId="exampleForm.ControlTextarea1">
      <Form.Label>Example textarea </Form.Label>
      <Form.Control as="textarea" rows={3} onChange={actualizarestado} name="detalle" defaultValue={detalle} />
      </Form.Group>
          <Form.Group controlId="formBasicCheckbox">
            <Form.Check type="checkbox" label="Check me out" />
          </Form.Group>
          <Button variant="primary" onClick={()=>actualizarJuego()}>
            Enviar
          </Button>
        </Form> 
        </div>
      </div>
     );
}
 
export default Editarjuego;